<template>
  <div class="confirm-main">
    <Header title=''/>
    <p class="confirm-title">[]{{product.title}}</p> 
    <Box title=''> 
      <p>{{product.title}}</p>
      <p>{{order.route.calendar}}</p>
      <p>{{order.route.num}}{{order.route.num2}}</p>
      <p>: 2, {{order.route.isroom === '1'?'':''}}</p>
      </Box> 
      <Box  title=''>
        <p class="under">{{order.information.name}}</p>
        <p class="under radio"> {{order.information.sex}} </p>
        <p class="under">{{order.information.phone}}</p>
        <p class="under">{{order.information.email}}</p>
      </Box>
      <Box  title='' add=''>
        <div v-for="item in addpeople" :key="item.id">
           <p class="under people">{{item.name}}</p>
        </div>       
      </Box>
      <Box  title=''>
        <div class="checked"><el-checkbox :checked='safe.safe1' @change="checked1"></el-checkbox>  2 <span>177/</span></div>
         <div class="checked"><el-checkbox :checked='safe.safe2' @change="checked2"></el-checkbox>  2 <span>177/</span></div>
      </Box>
       <div class="fill-confirm">
        <span></span>
      </div>
      <div class="confirm-footer">
        <span>{{cost}}</span> <router-link :to='`/product/${product.id}/payment`'></router-link>
      </div>
  </div>
</template>

<script>
import Header from './Header'
import {mapState,mapGetters,mapMutations} from 'vuex'
import Box from './Box'
export default {
    name:"ConfirmOrder",
    data(){
      return{
          radio: '1',
          num10:6,
          activeName: 'first',
      }
    },
    components:{
      Header,
      Box
    },
    computed:{
     ...mapState({
       products:state=>state.ProductModule.product,
       order:state=>state.MineModule.Order,
       addpeople:state=>state.MineModule.Order.tourist,
       signup:state=>state.MineModule.Order.route,
       safe:state=>state.MineModule.Order.safe
     }),
     ...mapGetters(['cost']),
       product(){
          return this.products.filter(item=>item.id === this.$route.params.id)[0]
        }
    },
    methods:{
      ...mapMutations(['checked1','checked2']),
    }

}
</script>

<style lang='scss'>
.confirm-main{
  background-color: #f5f5f5;
  padding-bottom: 100px;
  .confirm-title{
    height: 50px;
    margin: 0;
    line-height: 50px;
    padding-left: 15px;
    background-color: #fff;
    margin-bottom: 10px;
  }
  
  }
  .confirm-footer{
    width: 86%;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content:space-between;
    padding:5px 7%;
    height: 50px;
    line-height: 50px;
    background-color: #fff;
    span{
      color: #f08211;
    }
    
    a{
      padding: 0px 32px;
      text-decoration: none;
      color: #fff;
      background-color: #50bbdb;
      border-radius: 8px;
    }
  }
  .fill-confirm{
  text-align: center;
  .fill-confirm-checkbox{
    margin-right: 6px;
  }
}
</style>