<template>
  <div class="fill-main">
    <Header title=''/>
    <p class="fill-title">[]{{product.title}}</p> 
    <Box title=''> 
      <p>{{product.title}}</p>
      <p>{{signup.calendar}} <button @click="linksignup" class="modify"></button></p>
      <p>{{signup.num}}{{signup.num2}}</p>
      <p><el-input-number class="input-number"  v-model="num3"   size="small" :min="0" :max="10" label=""></el-input-number></p>
      <p class="order-room"> <span class="room-select">:</span> <span><el-radio v-model="radio" label="1"></el-radio>
        <el-radio v-model="radio" label="2">3500</el-radio></span> </p>
      </Box> 


       <div class="fill-box">
       <el-tabs v-model="activeName">
       <el-tab-pane label="" name="first"><div> 
            <h4></h4>
            <p>*+7x24/
             <br>*+
             <br>*++17
             <br>*+
             <br>*+1</p>
            </div> </el-tab-pane>
            <el-tab-pane label="" name="second"><div> 
            <h4></h4>
            <p>*+7x24/2
             <br>*+2
             <br>*++172
             <br>*+2
             <br>*+2</p>
            </div> </el-tab-pane>
           </el-tabs>
      </div>


      <Box  title=''>
        <p class="under"><input type="text" v-model="value" placeholder=""></p>
        <p class="under radio">  <el-radio v-model="radio1" label=""></el-radio>
        <el-radio v-model="radio1" label=""></el-radio></p>
        <p class="under"><input  v-model="value2" type="text"></p>
        <p class="under"><input  v-model="value3" type="text" placeholder=""></p>
      </Box>

      <Box  title='' add=''>
        <p class="under"> <input  v-model="value4" type="text"></p>
        <p class="under"><input  v-model="value5" type="text"></p>
        <div  v-for="item in addpeople" :key="item.id">
          <p class="under"> <input  v-model="item.name" type="text"></p>
        <p class="under"><input  v-model="item.number" type="text"></p>
        </div>
      </Box>

      <Box  title=''>
        <div class="checked"><el-checkbox :checked='safe.safe1' @change="checked1"></el-checkbox>  2 <span>177/</span></div>
         <div class="checked"><el-checkbox :checked='safe.safe2'  @change="checked2"></el-checkbox>  2 <span>177/</span></div>
      </Box>

      <div class="fill-confirm">
        <input type="checkbox" v-model="checked" class="fill-confirm-checkbox"><span></span>
      </div>

      <div class="fill-footer">
        <span>{{cost}}</span> <a><span class="confirm-fill" @click="addProducts({route:{num:signup.num,num2:signup.num2,calendar:signup.calendar,roomnum:num3,isroom:radio},information:{ name:value, sex:radio1,phone:value2,email:value3}, tourist:{id:new Date().getTime(),name:value4,number:value5}})"></span></a>  
      </div>
  </div>
</template>
s
<script>
import Header from './Header'
import {mapState,mapMutations,mapGetters} from 'vuex'
import Box from './Box'
import {Message} from 'element-ui'
export default {
    name:"Fillin",
    data(){
      return{
         radio: '1',
         radio1: '',
         activeName: 'first',
         signupmsg:null,
         num3:1,
         value:'',
         value2:'',
         value3:'',
         value4:'',
         value5:'',
         checked:''    
      }
    },
    components:{
      Header,
      Box
    },
    computed:{
     ...mapState({
       products:state=>state.ProductModule.product,
       addpeople:state=>state.MineModule.Order.tourist,
       signup:state=>state.MineModule.Order.route,
       safe:state=>state.MineModule.Order.safe
     }),
     ...mapGetters(['cost']),
       product(){
       return this.products.filter(item=>item.id === this.$route.params.id)[0]
     }
    },
    methods:{
      ...mapMutations(['addProduct','checked1','checked2']),
      linksignup(){
       this.$router.push(`/product/${this.product.id}/signup`)
      },
      addProducts(paylode){
        if(!this.value || !this.value2 || !this.value3 || !this.value4 || !this.value5){
           Message('');
        }
        else if(!this.checked){
            Message('');
        }
        else{
          this.$router.push(`/product/${this.product.id}/confirm`)
          this.addProduct(paylode)
        }
        
      }
    }
}
</script>

<style lang='scss'>
.fill-main{
  background-color: #f5f5f5;
  padding-bottom: 100px;
  .fill-title{
    height: 50px;
    margin: 0;
    line-height: 50px;
    padding-left: 15px;
    background-color: #fff;
    margin-bottom: 10px;
  }
  .fill-box{
      width: 80%;
     margin: 0 auto; 
     padding:0 5%;  
     border-radius: 8px;
     background-color: #fff;
     margin-bottom: 10px;
     background-image: url('../img/order-detail-bgc.png');
     background-repeat: no-repeat;
     background-position: 12px 12px;
     background-size: 2px 18px;
     font-size: 14px;
     line-height: 30px;
     h4{
       margin: 0;
     }
  }
  .fill-footer{
    width: 86%;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content:space-between;
    padding:5px 7%;
    height: 50px;
    line-height: 50px;
    background-color: #fff;
    span{
      color: #f08211;
    }
    .confirm-fill{
    color: #fff;
    }
    
    a{
      padding: 0px 32px;
      text-decoration: none;
      color: #fff;
      background-color: #50bbdb;
      border-radius: 8px;
    }
  }
}
.fill-confirm{
  text-align: center;
  .fill-confirm-checkbox{
    margin-right: 6px;
  }
}

</style>