<template>
  <div>
  <Header title="" right='' @add='add'/>
  <div class="sucess-title"></div>
  <div class="sucess-content">
      <div>
         <p> <span></span><span>{{information.name}}</span></p> 
         <p><span></span><span>{{product.title}}</span></p> 
      </div>
      <div>
          <p> <span></span><span>{{payment}}</span></p>
          <p> <span></span><span>12245577864565699999999</span></p> 
         <p><span></span><span>{{currenttime}}</span></p> 
      </div>
      <div>
         <p><span></span><span>{{cost}}.00</span></p> 
      </div>
  </div>
  <div class="sucess-footer">
      <p>7</p>
      <button @click="back"></button>
  </div>
  </div>
</template>

<script>
import moment from 'moment'
import {mapState,mapGetters} from 'vuex'
import Header from './Header'
export default {
    name:"Sucess",
    components:{
        Header,
        payment:''
    },
    created(){
    this.payment = localStorage.getItem('payment')
    },
    computed:{
    ...mapState({
         products:state=>state.ProductModule.product,
         information:state=>state.MineModule.Order.information
    }),
    ...mapGetters(['cost']),
     product(){
          return this.products.filter(item=>item.id === this.$route.params.id)[0]
        },
        currenttime(){
        return  moment().format('YYYY-MM-DD HH:mm:ss')
        }
      },
    methods:{
        back(){
            this.$router.push('/')
        },
        add(){
          this.$router.push('/')  
        }
    }

}
</script>

<style lang='scss'>
.sucess-title{
    text-align: center;
    color: #50bbdb;
    height: 60px;
    line-height: 60px;
    margin-bottom: 10px;
     border-bottom: 1px solid #ccc;
     background-image: url('../img/sucess.jpg');
     background-repeat: no-repeat;
     background-position: 30% center;
     background-size: 24px 24px;
}
.sucess-content{
width: 100%;
div{
    padding: 10px 20px;
    border-bottom: 1px solid #ccc;
p{
   display: flex;
    justify-content: space-between;
    margin:8px 0;  
}
   
}
}
.sucess-footer{
   padding: 10px 20px;
   background-color: #f5f5f5;  
   padding-bottom: 200px;
   button{
       display: block;
       width: 100%;
       border: 1px solid #ccc;
       outline: 0;
       color: #50bbdb;
        background-color: #f5f5f5; 
       font-size: 20px;
       text-align: center;
       height: 50px;
       line-height: 50px;
   }
}
</style>